.cbx-page-wrapper{
  margin-top: 50px;

  .container, #poststuff {
    max-width: 1200px;
    margin: 0 auto;
  }

  .postbox{
    border-radius: 8px;
    border: 0 none;
    box-shadow: 0 10px 22px 0 rgba(0, 4, 32, .05) !important;
  }
}


.cbx-page-listing{
  .postbox{
    border-radius: 8px;
    border: 0 none;
    box-shadow: 0 10px 22px 0 rgba(0, 4, 32, .05) !important;
  }
}


.cbx-chota {
  .tags:not(.wp-list-table){
    display: flex;
    align-items: start;

    .tag {
      font-size: 15px !important;
      line-height: 1.2 !important;
      margin: 3px 0 3px 10px !important;
      padding: 3px 8px !important;
      cursor: pointer;

      &:first-child {
        margin-left: 0;
      }

      b {
        height: 15px;
        width: 15px;
        font-size: 15px;
        line-height: 1;
        cursor: pointer;
        display: inline-block;
        vertical-align: baseline;
        text-align: center;

        &:hover {
          background-color: red !important;
          color: #fff !important;
        }
      }
    }
  }

  .cbx-sub-heading-wrap{
    display: flex;
    flex-wrap: wrap;
    .cbx-sub-heading-l{
      margin-right: auto;
      display: flex;
      align-items: center;
      justify-content: flex-start;
    }
    .cbx-sub-heading-r{
      margin-left: auto;
      display: flex;
      align-items: center;
      justify-content: flex-end;
    }
    /*.cbx-sub-heading {
      font-size: 20px !important;
      line-height: 1.2 !important;
    }*/
  }


  //modal picker select wrapper style
  .xyz-filter-wrap {
    display: flex;

    .xyz-filter-selected-wrap {
      display: flex;

      .xyz-filter-selected {
        border-right: 0 !important;
        border-radius: 4px 0 0 4px !important;
      }

      .xyz-filter-selected-remove {
        border-radius: 0 4px 4px 0 !important;
        margin-left: 0 !important;
        cursor: pointer;
      }
    }

    &.user-filter-wrap{
      align-items: center;
    }
  }

  .treeselect-input__edit{
    font-size: 12px;
    line-height: 2;
    padding-left: 5px;
    padding-right: 5px;
    border:0 !important;
  }

  .treeselect-list__item-checkbox{
    margin: 0 !important;
  }

  .vue-treeselect__list-item{
    .vue-treeselect__option{
      .vue-treeselect__label-container{
        //vue-treeselect__checkbox--checked
        //vue-treeselect__checkbox--unchecked
        .vue-treeselect__checkbox{
          .vue-treeselect__check-mark{}
          .vue-treeselect__minus-mark{}
        }
      }

      &.vue-treeselect__option--highlight{
        .vue-treeselect__label-container{
          //vue-treeselect__checkbox--checked
          //vue-treeselect__checkbox--unchecked
          .vue-treeselect__checkbox{
            .vue-treeselect__check-mark{
              background-color: red;
            }
            .vue-treeselect__minus-mark{
              background-color: green;
            }
          }
        }
      }
    }
  }
}
