.cbx-chota {
  .vfm--fixed {
    position: fixed;
  }
  .vfm--absolute {
    position: absolute;
  }
  .vfm--inset {
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
  }
  .vfm--overlay {
    background-color: rgba(0, 0, 0, 0.5);
  }
  .vfm--prevent-none {
    pointer-events: none;
  }
  .vfm--prevent-auto {
    pointer-events: auto;
  }
  .vfm--outline-none:focus {
    outline: none;
  }
  .vfm-fade-enter-active,
  .vfm-fade-leave-active {
    transition: opacity .3s;
  }
  .vfm-fade-enter-from,
  .vfm-fade-leave-to {
    opacity: 0;
  }
  .vfm-bounce-back {
    transition-property: transform;
    transition-duration: .3s;
  }
  .vfm-slide-up-enter-active,
  .vfm-slide-up-leave-active,
  .vfm-slide-down-enter-active,
  .vfm-slide-down-leave-active {
    transition: transform .3s ease;
  }
  .vfm-slide-down-enter-from,
  .vfm-slide-down-leave-to {
    transform: translateY(100%);
  }
  .vfm-slide-up-enter-from,
  .vfm-slide-up-leave-to {
    transform: translateY(-100%);
  }
  .vfm-slide-right-enter-active,
  .vfm-slide-right-leave-active,
  .vfm-slide-left-enter-active,
  .vfm-slide-left-leave-active {
    transition: transform .3s ease;
  }
  .vfm-slide-right-enter-from,
  .vfm-slide-right-leave-to {
    transform: translateX(100%);
  }
  .vfm-slide-left-enter-from,
  .vfm-slide-left-leave-to {
    transform: translateX(-100%);
  }
  .vfm-swipe-banner-back,
  .vfm-swipe-banner-forward {
    position: fixed;
    top: 0;
    bottom: 0;
    width: 27px;
    z-index: 10;
  }
  .vfm-swipe-banner-back {
    left: 0;
  }
  .vfm-swipe-banner-forward {
    right: 0;
  }

  //custom css
  .vfm__content.modal-content {
    display: flex;
    flex-direction: column;
    max-height: 90%;
    min-width: 350px;
    width: 50%;
    margin: 50px auto;
    padding: 1rem;
    border: 1px solid #e2e8f0;
    border-radius: 0.25rem;
    background: #fff;
    overflow-x: auto;
    position: relative !important;

    .modal__header{
      display: flex;
      .modal__title {
        font-size: 20px !important;
        line-height: 1.5 !important;
        font-weight: normal !important;
        margin: 0 auto 0 0 !important;
      }

      .modal__close {
        cursor: pointer !important;
        margin-left: auto;
      }
    }


    .modal__content {
      flex-grow: 1;
      margin-top: 20px !important;
    }

    .modal__action {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-shrink: 0;
      padding: 1rem 0 0;
    }


  }
}
