.cbxwpbookmark-frontend-manager-wrapper{
  background-color: #f0f0f1;
  padding: 12px;
  border-radius: 16px;
}

.cbxwpbookmark-frontend-manager-wrapper-inside{
  background-color: #ffffff;
  border-radius: 12px;
  padding: 20px;
}

.cbx-chota {
  a {
    cursor: pointer;
  }

  .cbx_builder_actions {
    .cbx_builder_actions_wrap{
      //margin: 20px 10px;
      margin: 0 0 10px 0;
      //max-width: 1200px;
      padding: 10px;
      background-color: #fff;
      border: 1px solid #ccc;
      border-radius: 8px;
    }

    h2, h3, h4, h5, h6{
      margin-bottom: 0 !important;
    }

    .col-8, .col-4{
      margin-bottom: 0 !important;
    }
  }

  #cbxwpbookmark-edit-main {
    background-color: #fff;
    //border: 1px solid #ccc;
    padding: 10px;

    border-radius: 8px;
    border: 0 none;
    box-shadow: 0 10px 22px 0 rgba(0, 4, 32, .05) !important;
  }

  #cbxwpbookmark-edit-message {
    background-color: #fff;
    border: 1px solid #ccc;
    padding: 30px;

    .success-icon {
      width: 90px;
      height: auto;
    }

    .cbxwpbookmark-event_create_success_message {
      margin: 15px 0 !important;
    }

    .cbxwpbookmark-event_create_success_details_msg {
      font-size: 16px;
      color: #489157;
    }
  }


  #cbxwpbookmark-edit-sidebar {
    .cbxwpbookmark-sidebar-widgets {
      .cbxwpbookmark-sidebar-widget {
        border-radius: 8px;
        border: 0 none;
        box-shadow: 0 10px 22px 0 rgba(0, 4, 32, .05) !important;

        background-color: #fff;


        margin-bottom: 20px;

        .cbxwpbookmark-sidebar-heading {
          padding: 5px 10px !important;
          //border: 1px solid #ccc;
          //border-width: 1px 1px 0 1px;
          margin-bottom: 0 !important;
          border-bottom: 1px solid #ccc;
        }

        .cbxwpbookmark-sidebar-content {
          padding: 10px;
          //border: 1px solid #ccc;
          label{
            margin-bottom: 0 !important;
            display: block;

            &.label-upload{
              margin-bottom: 10px !important;
            }
          }

        }
      }
    }
  }

  #cbxwpbookmark_builder_form {
    background-color: #fff;
    //padding: 10px;
    margin: 20px auto 10px 0;
    width: 100%;
    max-width: 1200px;

    &.transparent {
      background-color: transparent;
    }
  }

  .cbxwpbookmark-select-inline {
    display: inline-block !important;
    width: auto !important;
  }

  .cbxwpbookmark-select-full {
    width: 100% !important;
  }

  #cbxwpbookmark_builder_form_cats_wrap {
    position: relative;
    width: 250px;

    @media only screen and (max-width: 600px) {
      width: 100%;
    }

    .cbxwpbookmark_builder_form_cats_heading {
      margin-bottom: 0;
      width: 100%;
      display: block;

      a {
        float: right;
        cursor: pointer;
        display: inline-block;
        border: 1px solid #ccc;
        width: 25px;
        height: 25px;
        line-height: 25px;
        text-indent: -99999px;

        background-size: auto !important;
        background-repeat: no-repeat !important;
        width: 25px;
        height: 25px;
        background-image: url("data:image/svg+xml;base64,");
        background-position: center;
        background-size: cover;
      }

      a.open {
        background-image: url("data:image/svg+xml;base64,");
      }
    }


    #cbxwpbookmark_builder_form_cats {
      position: absolute;
      top: 30px;

      padding: 10px;
      background-color: #fff;
      width: 100%;


      box-shadow: 0 10px 22px 0 rgba(0, 4, 32, 0.05) !important;
      border-radius: 8px;
      border: 1px solid #ccc;
    }


  }

  #cbxwpbookmark_builder_form_cats {
    .cbxwpbookmark_builder_form_cat {
      display: block;
      margin-bottom: 5px;
      line-height: 25px;

      input {
        &:before {
          //margin: 0 !important;
        }
      }
    }
  }
}

