.cbx-chota{
  #dashlisting_toolbar{
    background-color: #EFF0FD;
    border: 1px solid #5d5dff33;
    border-radius: 10px;
    padding: 10px;
    box-shadow: 0 10px 22px 0 rgba(0,4,32,.05) !important;

    h2{
      margin-bottom: 0 !important;
    }
  }
}

//fix for theme 'twentytwentyfour'
.is-layout-constrained > .cbxwpbookmark-frontend-manager-wrapper:where(:not(.alignleft):not(.alignright):not(.alignfull)){
  max-width: 100% !important;
}

html.wp-toolbar  body.wp-admin{
  background-color: #e8f3fa;
}

#event_edit_navs{
  padding: 0;
  margin: 0 0 10px 0;
  list-style: none;
  li{
    list-style: none;
    display: inline-block;
    margin-right: 10px;
    &:last-child{
      margin-right: 0;
    }
  }
}

//theme 'twentytwentyfour' fix
.is-layout-constrained > .cbxwpbookmark-frontend-manager-wrapper:where(:not(.alignleft):not(.alignright):not(.alignfull)){
  max-width: 100%;
}

//theme 'twentytwenty' fix
.entry-content > .cbxwpbookmark-frontend-manager-wrapper:not(.alignwide):not(.alignfull):not(.alignleft):not(.alignright):not(.is-style-wide){
  width: 100%;
  max-width: 1500px;
  margin: 0 auto;
  .container{
    max-width: 100% !important;
  }
}